@import "https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400&display=swap";
body {
  color: #898fa1;
  font-family: Lato, sans-serif;
  font-size: 15px;
  line-height: 1.8;
}

h1, h2, h3, h4, h5, h6 {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #11245a;
  font-weight: 100;
}

.title-sm {
  letter-spacing: 1px;
  font-size: 16px;
}

a {
  font-weight: 700;
  text-decoration: none;
  transition: all .4s;
}

img {
  width: 100%;
}

small {
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
}

section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.navbar {
  border-bottom: 1px solid #0000001a;
}

.red-text {
  color: #6e0909;
  text-transform: uppercase;
  font-size: 20px;
}

.blue-text {
  color: #0a0a48;
  text-transform: uppercase;
  font-size: 16px;
}

.logo {
  width: 50px;
  height: auto;
  margin-right: 2px;
}

.navbar .nav-link {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 12px;
}

.navbar-light .navbar-nav .nav-link.active {
  color: #b32e2e;
}

#home {
  background-color: #0000;
  background-image: linear-gradient(#11245acc, #11245acc), url("https://www.mtu.edu/chemical/facilities/teaching/operations/images/che-uo-slide-hands-on-1024feature.jpg");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: fixed;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

#home h1 {
  font-weight: 700;
}

#home p {
  max-width: 550px;
  margin: 18px auto;
}

.btn {
  text-transform: uppercase;
  border-radius: 20px;
  padding: 12px 28px;
  font-size: 12px;
  font-weight: 700;
}

.btn-brand {
  color: #fff;
  background-color: #b32e2e;
}

.btn-brand:hover {
  color: #fff;
  background-color: #c03a3a;
}

.about-us-container {
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 2rem auto;
  padding: 1rem;
  display: flex;
}

.about-us-text, .about-us-image {
  flex: 50%;
  padding: 1rem;
}

@media only screen and (width <= 768px) {
  .about-us-text, .about-us-image {
    flex: 1 0 100%;
  }
}

.section-intro {
  text-align: center;
  margin-bottom: 60px;
}

.section-intro .hline {
  background-color: #0000001a;
  width: 120px;
  height: 2px;
  margin: 16px auto 0;
}

.icon-box {
  color: #fff;
  background-color: #b32e2e;
  border-radius: 4px;
  flex: none;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  font-size: 32px;
  display: flex;
}

.portfolio-item {
  position: relative;
  overflow: hidden;
}

.portfolio-item img, .portfolio-item .portfolio-overlay {
  transition: all .4s;
}

.portfolio-overlay {
  opacity: 0;
  background-color: #11245ae6;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.portfolio-overlay h3, .portfolio-overlay h6 {
  color: #fff;
}

.portfolio-item:hover .portfolio-overlay {
  opacity: 1;
}

.portfolio-item:hover img {
  transform: scale(1.1);
}

.feature .icon-box {
  border-radius: 100px;
  width: 34px;
  height: 34px;
  font-size: 24px;
}

.team-member {
  background-color: #11245a;
}

.team-member.even {
  background-color: #162e6e;
}

.team-member .hline {
  background-color: #ffffff71;
  width: 70px;
  height: 2px;
  margin: 16px auto;
}

.team-member .team-member-img img, .team-member .team-member-img .social-icons {
  transition: all .4s;
}

.team-member .team-member-img {
  position: relative;
  overflow: hidden;
}

.team-member .team-member-img .social-icons {
  background-color: #b32e2e;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
  position: absolute;
  bottom: -60px;
  left: 0;
}

.team-member .team-member-img .social-icons a {
  color: #fff;
  border: 2px solid #ffffff80;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-left: 4px;
  margin-right: 4px;
  font-size: 24px;
  display: flex;
}

.team-member .team-member-img .social-icons a:hover {
  color: #e93b81;
  background-color: #fff;
}

.team-member:hover .team-member-img .social-icons {
  bottom: 0;
}

.team-member:hover img {
  transform: translateY(-60px);
}

#testimonials img {
  border-radius: 100px;
  width: 65px;
  height: 65px;
  transition: all .4s;
}

#testimonials .nav-pills .nav-link.active {
  background-color: #0000;
}

#testimonials .nav-pills .nav-link.active img {
  transform: scale(1.3);
}

.review .stars {
  color: #b32e2e;
}

.review p {
  max-width: 720px;
  margin: 24px auto;
  font-style: italic;
}

#clients img {
  height: 35px;
}

form .form-control {
  border-radius: 0;
}

form .form-control:focus {
  box-shadow: none;
  border-color: #e93b81;
}

#cta {
  background-color: #e93b81;
}

footer {
  border-top: 1px solid #0000001a;
  padding-top: 20px;
  padding-bottom: 20px;
}

footer .footer-links a, footer .social-icons a {
  color: #898fa1;
}

footer .social-icons a {
  font-size: 24px;
}

footer .footer-links a {
  font-weight: 400;
  display: block;
}

footer .social-icons a:hover, footer .footer-links a:hover {
  color: #e93b81;
}

.cust_parent {
  position: relative;
}

#prevButton, #nextButton {
  color: #942020;
  background-color: #0000;
  border: none;
  outline: none;
  height: 40px;
  padding: 5px 2px;
  font-size: 24px;
  position: absolute;
  top: 3rem;
  transform: translateY(-50%);
}

#prevButton:focus, #nextButton:focus {
  box-shadow: none !important;
  outline: none !important;
}

#prevButton {
  bottom: 20px;
  left: 380px;
}

#nextButton {
  bottom: 20px;
  right: 380px;
}

#prevButton:hover, #nextButton:hover {
  color: #a63e3e;
  cursor: pointer;
}
